*{
    font-family: 'Roboto', sans-serif;
    font-size: 18px;
}
body{
    display: flex;
    justify-content: center;
    align-items: center;
}
.container{
    top: 50%;
    left: 50%;
    position:absolute;
    transform: translate(-50%, -50%);
    margin: 0 auto;
    width: 256px;
    text-align: center;
    border: 10px solid black;
    background-color: black;
    border-radius: 5px;

}
.keypad{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-auto-rows: minmax(60px, auto);
}
input[type="text"]{
    height: 70px;
    width: 249px;
    background-color: white;
    border:0px;
    color: black;
    text-align: right;
    font-size: 25px;
    font-weight: 500;
    letter-spacing: 1px;
}
#clear{
    grid-column: 1/3;
    grid-row:1 ;
    color: rgb(43, 41, 41);
}
#result{
    grid-column: 3/5;
    grid-row: 5;
    color: rgb(43, 41, 41);
}
#backspace{
    color: rgb(43, 41, 41);
}
button{
    margin: 5px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    background-color: rgb(43, 41, 41);
    color: white;
    font-weight: 500;
}

input:focus,
input:active,
button:focus,
button:active{
    outline: none;
}
.highlight{
    background: cyan;
}
